<template>
    <b-container class="bv-example-row">
        <b-row><b-col><input type="text" v-model="searchQuery" placeholder="Search..." class="search"></b-col><router-link to="/logout">Logout</router-link></b-row>
        <b-row v-if="!isMobile()">
            <b-col sm="5" class="scroll">
                <div v-for="(recipe,i) in resultQuery" :key="i" class="listitem" @click="selected = recipe">
                    <img v-if="recipe.image" v-bind:src="'data:image/png;base64,'+recipe.image" />
                    <img v-else width="44" height="44" src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mP8Xw8AAoMBgDTD2qgAAAAASUVORK5CYII=" /> {{recipe.title}}
                </div>
            </b-col>
            <b-col sm="7" v-if="selected.title" class="scroll">
                <b-button v-if="isMobile()" @click="selected = {}">Back</b-button>
                <h1>{{selected.title}}</h1>
                <p v-html="nl2br(selected.body)"></p>
            </b-col>
        </b-row>
        <b-row v-else>
            <b-col sm="4" class="scroll" v-if="!selected.title">
                <div v-for="(recipe,i) in resultQuery" :key="i" class="listitem" @click="selected = recipe">
                    <img v-if="recipe.image" v-bind:src="'data:image/png;base64,'+recipe.image" />
                    <img v-else width="44" height="44" /> {{recipe.title}}
                </div>
            </b-col>
            <b-col sm="8" v-if="selected.title" class="scroll">
                <b-button v-if="isMobile()" @click="selected = {}">Back</b-button>
                <h1>{{selected.title}}</h1>
                <p v-html="nl2br(selected.body)"></p>
            </b-col>
        </b-row>
    </b-container>
</template>

