        "body": "30 dkg liszt\n    25 dkg Rama\n    2 tk só\n    1-1,5 dl tejföl\n    1 tojás a tetejére\n    sajt\n    A lisztet összegyúrjuk a margarinnal, sóval és annyi tejföllel, amennyit felvesz.\n    Kinyújtjuk kb. ujjnyi vastagra, megkenjük a sózott felvert tojással, megszórjuk reszelt sajttal.\n    Éles késsel, pizzavágóval vagy derelyevágóval hosszúkás rudakra vágjuk.\n    Sütőpapírral bélelt tepsire rakjuk, és előmelegített sütőben aranybarnára sütjük 200 fokos sutoben kb 15 perc.",
