        "body": "3 csésze BL 80-as liszt (BL55-össel is működik)\n1-1,5 csésze víz\nkicsit kevesebb, mint fél tk szárított élesztő\n1,5 tk só \n    A liszthez keverjük a sót és az élesztőt, jól elkeverjük őket (mivel többet nem nagyon kell keverni).\n    Hozzáöntjük a vizet, elkeverjük kanállal (8-10 fordulat), hogy egyenletesen nedves legyen mindenhol a lisztes keverék. Ezután 12 órát kelesztjük (célszerű este/kora reggel elkészíteni). Az állaga nokedli tésztához hasonló, ne gyúrható tésztára számítsunk!\n    12 óra múlva kovászszerű massza lesz belőle, kiöntjük a tálból lisztezett deszkára (konyhapultra), áthajtogatjuk, mindkét oldalát belisztezzük, letakarjuk a kelesztéshez használt edénnyel, és további 15-20 percig pihentetjük. Ezalatt a sütő is bemelegszik.\n    A sütőt felmelegítjük, egy fedeles, jénai edényt előmelegítünk a sütőben.  \n    Az előmelegített tálat liszttel behintjük, és beletesszük a tésztát. Lefedve, 35 percig sütjük, majd fedő nélkül 8-10 percig. Ilyenkor inkább figyeljük, nehogy megégjen.",
