        "body": "50 dkg liszt\n3 dl víz\n2,5 dkg élesztő\n1/2 evőkanál cukor\n1 teáskanál só\n1 tojás\n+1 a kenéshez\nA hozzávalókat beletettem a kenyérsütőgépbe a dagasztó programra, ami nagyon szépen összekeverte és megkelesztette. Után kiszedtem a tésztát a gépből és zsemleformákat igyekeztem formálni a tésztából. Hagytam még egy kicsit pihenni őket, majd megkentem tojással. Sütőben 220 fokon sütöttem először 5 percig, majd 180 fokon még 10 percet.\n12 db",
