        "body": "1100ml flour\n35ml olive oil\n10ml salt\n300ml water\n1/3 yeast cube (16g)\nIn a large bowl, dissolve the sugar in warm water, and then stir in yeast. Allow to proof until yeast resembles a creamy foam.\nMix salt and oil into the yeast. Mix in flour one cup at a time. Knead dough on a lightly floured surface until smooth. Place it into a floured bowl, cover with a damp cloth. Allow to rise until doubled in bulk, about 1 hour.\nPut in a shape, allow to rise it for 30 minutes more, or until dough has risen 1 inch above pans.\nBake it at 350 degrees F (175 degrees C) for 30 minutes.",
        "category": "Breads",
        "timestamp": "2009-11-11T22:02:35Z",
        "title": "White bread (sample recipe)"
