        "body": "A tésztához:\n    50 dkg liszt\n    8 dkg margarin\n    10 dkg porcukor\n    2 dkg szalalkáli\n    2 dl tej\nA krémhez:\n    35 dkg cukor\n    1 l tej\n    5 dkg kakaópor (pl. szerencsi cicás)\n    6 ek liszt\n    10 dkg margarin vagy vaj\n    1 cs vaníliás cukor\nA nyújtahtó fondanthoz:\n    1 ek zselatin\n    6 ek víz\n    1 ek margarin\n    2 ek méz\n    0,5 kg porcukor + a gyúráshoz\nA díszítéshez:\n    15 dkg marcipán\n    ételfestékek\n    étcsokoládé\n    ecset\n    fogpiszkálók\n    1 nagyobb burgonya fele\n    alufólia\n    A tészta elkészítése: a lisztet a margarinnal és a cukorral elmorzsoljuk, vagy robotgéppel összedolgozzuk. A tejben a szalalkálit felfuttatjuk és a liszthez adjuk. Összedolgozzuk rétestészta keménységűre. Elosztjuk 6 egyenlő részre, amiből cipókat gyúrunk. Majd egyesével vékonyra kinyújtjuk. A sütőt előmelegítjük. Lisztezett tepsi hátlapján pár perc alatt laponként megsütjük. (Az újabb típusú sütőkben kb. 5 perc alatt sül meg egy-egy lap.)\n    A krém elkészítése: a tej 3/4 részét a cukorral és a vaníliával felforraljuk. A lisztet a kakaóval és a maradék tejjel simára keverjük, majd a forrásban lévő tejhez öntjük. Folytonos keverés mellett sűrű krémmé főzzük. A krémet a tűzről levesszük, és a vaj felét elkeverjük benne.\n    Még melegen a lapokra kenjük szintenként. A tetejét a maradék olvasztott vajjal megkenjük. Hideg helyen egy napig pihentetjük. Másnap a kívánt formára vágjuk a tortát.\n    A nyújtható fondant elkészítése: fontos, hogy legalább egy nappal előbb elkészítsük, mielőtt dolgozni szeretnénk vele! A porcukrot tálba szitáljuk, a közepébe mélyedést teszünk. Ha robotgéppel dolgozunk, akkor nem szükséges mélyedést készíteni. A zselatint elkeverjük a vízzel, és gőz felett felmelegítjük. Hozzáadjuk a mézet és a margarint, összemelegítjük. Utána a porcukorhoz adjuk és addig dolgozzuk, amíg egyenletesen összeáll, kb. keményebb gyurma állagúra. Frissentartó fóliába tekerjük, és 1 napra a hűtőbe tesszük.\n    Másnap porcukorral meghintett deszkán átgyúrjuk, hogy puha, könnyen formázható állagú gyurmaszerű anyagot kapjunk, és utána vékonyra kinyújtjuk. Utána díszítjük a formára vágott tortát. Ez az adag egy kb. 30 cm átmérőjű torta bevonására elég. (Tipp 1: Színes fondant készítéséhez a 6 ek víz egy részét folyékony ételfestékkel kell helyettesíteni. Tipp 2: Ha a fondant túl kemény lenne, mikor a hűtőből kivesszük, akkor mikróhullámú sütőben olvasztó programon 1-2 perc alatt újból felpuhítjuk, és átgyúrjuk.)\n    A díszítés elkészítése: a marcipánból egyforma golyókat készítünk, ezeket formázzuk. Az aljukba fogpiszkálót szúrunk, és egy félbevágott alufóliába csomagolt burgonyába szúrjuk őket. Ezután az ecsettel és az ételfestékkel színezzük a figurákat, és visszatűzzük őket a burgonyába. Egy napot hagyjuk száradni.\n    Másnap gőz felett olvasztunk 4-5 kockányi étcsokoládét, és azzal festjük a szemüket és a szemöldöküket, valamint a hóember gombjait. A hóember és a hógolyók a maradék fondantból készültek. A hóembert fogpiszkálókkal rögzítjük szintenként, az aljába 3 db fogpiszálót teszünk, és így állítjuk a tortába. A madarakat, a malacokat és a hógolyókat szintén fogpiszkálóval rögzítjük.\nsütési hőfok:\n200°C\nsütési mód:\nalul-felül sütés\ntepsi mérete:\n35×30 cm\nköltség:\n3000 Ft",
