    {
        "body": "- 400g unsalted butter, chopped\n- 400g dark chocolate, broken into pieces\n- 2 cup brown sugar\n- 6 eggs, lightly beaten\n- 2 teaspoon vanilla extract\n- 1.5 cup gluten-free plain flour\n- 4 tablespoons cocoa powder\nPreheat oven to 190°C. Line a 5cm deep, 18cm (base) square cake pan with non-stick baking paper.\nHeat butter, chocolate and sugar in a saucepan over low heat, stirring constantly, until melted and smooth. Transfer to a heatproof bowl. Set aside to cool slightly.\nAdd eggs and vanilla to chocolate mixture. Mix well. Sift flour and cocoa over chocolate mixture. Stir to combine.\nPour brownie mixture into pan. Bake for 20 minutes or until just set. Set aside to cool. Once cooled, lift out. Wrap in plastic wrap. Place into an airtight container. Stand for 1 day. Cut into pieces. Serve.",
        "category": "Cakes",
        "cloudFilename": "Brownie",
        "timestamp": "2012-12-02T13:27:36Z",
        "title": "Brownie"
    },
    {
        "body": "2/3 cup white sugar\n1/3 cup dark brown sugar\n1 teaspoon baking powder\n3 cups all-purpose flour\n1/2 cup shortening\n1/2 cup butter\n1 large egg\n1 pinch ground cinnamon\n4 cups fresh or frozen cranberries\n2/3 cup white sugar\n2 tablespoons cornstarch\nPreheat the oven to 375F. Grease a 9x13 inch pan.\nIn a medium bowl, stir together brown sugar, 2/3 cup white sugar, flour, baking powder and cinnamon. Use a fork or pastry cutter to blend in the shortening and egg. Dough will be crumbly (like damp sand). Pat half of dough into the prepared pan.\nIn another bowl, stir together the sugar and cornstarch. Gently mix in the cranberries. Sprinkle the cranberry mixture evenly over the crust. Crumble remaining dough over the berry layer.\nBake in preheated oven for 45 minutes, or until top is slightly brown. Cool completely before cutting into squares.",
