    {
        "body": "Hozzávalók 15 dl-hez:\n2 l víz\n1 közepes fej vöröshagyma\n150 g sárgarépa\n60 g zellergumó\n60 g petrezselyemgyökér\n3 db babérlevél\n5 szem egész feketebors\nízlés szerint só\n1. Mérjük ki a hozzávalókat.\n2. Hámozzuk meg, mossuk le a zöldségeket, és nagyjából egyforma darabokra vágjuk fel - a vöröshagymát elég félbevágni.\n3. Most mérjük ki a lábasba a receptben szereplő vízmennyiséget, és forraljuk fel. Ha felforrt a víz, tegyük bele a zöldségeket, a fűszereket, és a sót is. Közepes hőfokon - gyöngyözve - addig forraljuk a vizet fedő alatt, míg a zöldségek jó át nem főnek, meg nem puhulnak.\n4. A lefőzött zöldségalaplevet egy szűrőn keresztül szűrjük kancsóba, és máris kaptunk 1.5 liter gyönyörű zöldségalaplevet. A megfőtt zöldségeket akár köretként is tálalhatjuk valamilyen ételhez.",
