        "body": "60 g olvasztott vaj vagy sütőmargarin\n50 g porcukor\n40 g élesztő\n200 ml tej\n500 g liszt\n5 db tojássárga\nA sütéshez olaj\nA lisztet egy edénybe tesszük (érdemes átszitálni, és télen egy kicsit a mikróban meglangyosítani, hogy a tésztánk könnyebben megkeljen). \nA meleg tejet, a porcukrot, az élesztőt pedig egy másik edénybe, vagy bögrébe kimérjük, (alulra a porcukrot, rá a meleg tejet, és belemorzsoljuk az élesztőt), letakarjuk, és hagyjuk 15 percig pihenni, hogy felfusson, (ne tegyük huzatos helyre, nem tesz jót neki). \nA lisztre öntsük rá az élesztős tejet, az olvasztott vajat vagy sütőmargarint, és a tojások sárgáját. Egy fakanállal keverjük össze a tésztát, és addig gyúrjuk, illetve dagasszuk, amíg teljesen sima nem lesz. \nAz így kapott tésztát takarjuk le, és meleg helyre téve kelesszük duplájára kb 30 perc alatt, (huzatos helyre ne tegyük). Ha a tészta megkelt, vékony liszten nyújtsuk ujjnyi vastagságúra, és egy 6 cm átmérőjű kiszúróval szaggassuk ki a fánkokat. \nA maradék tésztát összegyúrjuk, újra kinyújtjuk, és kiszaggatjuk. A fánkokat kb. 15-20 percig letakarva egy konyharuhával pihentetjük, hogy kissé újra megkeljen, (így lesz jó könnyű a tészta). A közepüket kissé benyomkodjuk az ujjunkkal, hogy kis mélyedések legyenek, ebbe rakjuk majd a lekvárt. \nKis adagokban kisütjük őket a forró olajban. Az első oldalukat kb. 3 percig sütjük, majd megfordítjuk őket, és további 1-2 percig sütjük. Ügyeljünk, hogy az olaj ne legyen túl forró, mert sötét lesz a fánk! Sütés közben folyamatosan nézzük a fánkokat, hogy szép világosak maradjanak. \nA kész fánkokat a szalvétával fedett tálcáta szedjük, (hogy a felesleges olajat felszívja), majd porcukorral leszórjuk, és baracklekvárral töltve tálaljuk.",
        "category": "Desserts",
