    },
    {
        "body": "A tésztához:\n    réteslap (fagyasztott)\n    100 g vaj (olvasztott)\nA töltelékhez:\n    125 g tészta - szélesmetélt (száraz)\n    300 g tehéntúró (félzsíros)\n    50 g kristálycukor\n    50 ml tejföl (20%-os)\n    25 g vaj\n    2 db tojássárgája\n    2 db tojásfehérje\n    0.5 db citrom reszelt héja\n    2 teáskanál vanília eszencia\n    mazsola\nAz eperöntethez:\n    390 g eper (friss)\n    90 g kristálycukor\nTeljes elkészülési idő: 70 perc\nHozzávalók\nA tésztához:\n    réteslap (fagyasztott)\n    100 g vaj (olvasztott)\nA töltelékhez:\n    125 g tészta - szélesmetélt (száraz)\n    300 g tehéntúró (félzsíros)\n    50 g kristálycukor\n    50 ml tejföl (20%-os)\n    25 g vaj\n    2 db tojássárgája\n    2 db tojásfehérje\n    0.5 db citrom reszelt héja\n    2 teáskanál vanília eszencia\n    mazsola\nAz eperöntethez:\n    390 g eper (friss)\n    90 g kristálycukor\n\nElkészítés\n1 A sütőt előmelegítjük 170 C-ra, és előveszünk egy 22 cm átmérőjű tortaformát.\n2 A tortaforma kör alakját kivágjuk az egyik réteslapból, majd félretesszük.\n3 A tortaformát olvasztott vajjal kikenjük, majd minden lapot olvasztott vajjal megkenve, kibéleljük a karikát úgy, hogy mindenütt 3 réteg réteslap legyen.\n4 A mazsolát áztassuk be egy bögre vízben. Felhasználás előtt a vizet majd leöntjük róla!\n5 Először felrakunk 1 liter vizet forrni. Ha kezd zubogni teszünk bele egy csipet sót, öntünk bele egy evőkanál olajat. Majd a metéltet megfőzzünk a vízben kb. 3-4 perc alatt. Ha kész, leszűrjük, hideg vízzel leöblítjük, lecsepegtetjük, félretesszük.\n6 A túrót villával áttörjük.\n7 A túróhoz hozzákeverjük a cukor felét, a tojások sárgáját, a vajat, a citromhéjat, tejfölt, és a vanília kivonatot. Jó alaposan elkeverjük, akár habverőgép segítségével is. A keveréket félretesszük.\n8 A tojások fehérjéjét a maradék cukorral habbá verjük.\n9 A túrós keveréket összeforgatjuk a kiáztatott mazsolával és a tésztával, majd óvatosan hozzákeverjük a fehérjehabot is.\n10 A tölteléket a formába tesszük, és elsimítjuk a tetejét.\n11 A körbe lelógó réteslapokat rétegenként, lekenve az olvasztott vajjal ráhajtjuk.\n12 A kör alakúra vágott réteslappal lefedjük, és ezt is megkenjük vajjal.\n13 170 C-on 40-50 percig sütjük.\n14 Amíg sül a vargabéles, elkészítjük az öntet. Az epret összefőzzük a cukorral folyamatos kevergetés mellett, kis hőfokon. Addig főzzük, míg darabos szószt nem kapunk.\n15 Ha a béles kisült, melegen szeleteljük, porcukrozzuk, és az öntettel tálaljuk.",
