        "body": "Császármorzsa\nHozzávalók (2-3 személyre):\n30 dkg búzadara\n3 tojás\n4,5 dl víz (tej helyett)\n5-10 dkg mazsola\n10 dkg margarin\n10 dkg cukor\nsó\n(porcukor, lekvár)\nA cukor felét keverd bele a langyos tejbe, adj hozzá kis sót, majd áztasd bele a mazsolát és a búzadarát is. A tojások sárgáit keverd el a maradék cukorral, a fehérjékből pedig verj kemény habot. Amikor a dara megduzzadt (felszívta a tejet), keverd hozzá a tojássárgát és a habot is. Egy tágas serpenyőben hevítsd fel a zsiradékot, öntsd bele a\nmasszát, s (egy lapáttal) folyamatosan kevergesd, míg szét nem esik kisebb darabokra, morzsákra. (A császármorzsát forrón, porcukorral meghintve, esetleg valamilyen\nlekvárral, vagy gyümölcsszörppel szokás tálalni.)",
