        "body": "Delicious cookies, with double the amount of ingredients compared to the original. This is a recipe you can easily make with preschool, primary school kids (4-7)\n    100g caster sugar (normal granulated sugar will do too)\n    200g soft butter (best at room temperature, but works otherwise too)\n    2 eggs\n    2 teaspoons vanilla extract (or 2 packs of vanilla sugar)\n    250g self-raising flour (or plain flour with baking powder, read instructions on baking powder for qty)\n    140g oats (look for it around breakfast cereals and müsli in supermarkets)\n \nMelting Moments: oat cookies from BBC I can Cook (cooking with kids)﻿\n \nStep 1\nBefore cooking, wash your hands and put an apron on. Weigh and measure all the ingredients. There should be enough for 12 Melting Moments using the quantities shown.\n \nStep 2\nPut the sugar and butter together in a bowl and mix it together with the wooden spoon till fluffy.\n \nStep 3\nBreak the egg into the cup, whisk with a fork and add just half of it to the butter mix. Beat it with the wooden spoon.\n \nStep 4\nAdd the teaspoon of vanilla extract and the flour and mix all the ingredients together until all the flour has disappeared and it is a sticky mixture.\n \nStep 5\nBreak the dough into 12 lumps and roll each of them into balls – try to make them all a similar size.\nOr just take a tablespoonful of dough at the time from the bowl.\n \nStep 6\nPut the oats onto a plate then roll the balls in the oats and put them onto a baking tray that is pre-covered with baking paper. Squash the balls slightly with your hands.\n \nStep 7\nYou will need to ask a grown-up for help with this part ( :) ). Put the baking tray in a pre-heated oven at 160ºC fan/180ºC/gas 4 for 12–15 minutes, or until cooked.\n \nStep 8\nLeave the Melting Moments on the baking tray to cool down, then you can eat them!\n﻿\nAdapted from CBeebies http://www.bbc.co.uk/cbeebies/icancook/makes/moments/",
        "category": "Desserts",
        "cloudFilename": "Melting+Moments+Oat+Cookies",
        "timestamp": "2012-01-04T20:36:12Z",
        "title": "Melting Moments Oat Cookies"
    },
    {
        "body": "Muffin alaprecept Amerikából\nHozzávalók:\n2 pohár (2 dl-es) liszt\n1/2 pohár tej\n1/4 pohár olaj\n1/4 pohár cukor\n2 egész tojás\n2 csomag vaníliás cukor\n1 csomag sütőpor\nreszelt citromhéj\nElkészítés: A hozzávalókat egy tálban összekeverjük. Azért alaprecept, mert fantáziánk szerint tehetünk bele mazsolát, diót, mákot, apróra vágott gyümölcs- vagy csokoládédarabokat, illetve ezeket vegyesen.\nOlajozott formába tesszük, előmelegített sütőben 180-200 fokon kb. 25 percig (tűpróba!) sütjük.\nMegjegyzés:\nSzoktam a tésztába 1 csomag gesztenyét keverni, kis rumot és csokoládédarabkákat hozzáadni. Ilyenkor koszorú formában sütöm és csokimázzal leöntöm.",
