    {
        "body": "- 600ml, 1 pint milk\n- 45g, 2-3 oz semolina\n- 25g, 1oz caster sugar\n- 15g,  or a knob of butter\n- a pinch of freshly grated nutmeg (to taste)\nWell grease an oven-proof dish\nPut milk into pan and heat until lukewarm\nSprinkle in the semolina\nCook slowly, stirring all the time until it comes to the boil and thickens\nAdd sugar and butter\nRemove from the heat and pour into the oven-proof dish\nGrate (or sprinkle ready ground) nutmeg over the top\nPut into a moderate oven (160C or Gas 3) for 30 minutes",
        "category": "Desserts",
        "cloudFilename": "Semolina+puding",
        "timestamp": "2013-02-19T16:40:27Z",
        "title": "Semolina puding"
    },
    {
        "body": "Ingredients\n    110 g margarine\n    100 g sugar\n    120 ml molasses\n    1 egg yolk\n    250 g sifted all-purpose flour\n    3 g salt\n    2 g baking powder\n    2 g baking soda\n    1 g ground cinnamon\n    2 g ground cloves\n    2 g ginger\n    2 g ground nutmeg\nDirections\n    In a large bowl, cream together the margarine and sugar until smooth. Stir in molasses and egg yolk. Combine the flour, salt, baking powder, baking soda, cinnamon, cloves, ginger, and nutmeg; blend into the molasses mixture until smooth. Cover, and chill for at least one hour.\n    Preheat the oven to 350 degrees F (175 degrees C). On a lightly floured surface, roll the dough out to 1/4 inch thickness. Cut into desired shapes with cookie cutters. Place cookies 2 inches apart on ungreased cookie sheets.\n    Bake for 8 to 10 minutes in the preheated oven, until firm. Remove from cookie sheets to cool on wire racks. Frost or decorate when cool.\nSource:\nhttp://allrecipes.com/recipe/eileens-spicy-gingerbread-men/detail.aspx",
        "category": "Desserts",
