    {
        "body": "1 tbsp vegetable oil\n8 skinless boneless chicken thighs\n8 rashers smoked streaky bacon , cut into large pieces\n1 onion , halved and sliced\n250g pack baby button mushrooms\nhandful thyme sprigs\n2 tbsp plain flour\n400ml chicken stock\n200ml milk\n500g pack fresh puff pastry , or frozen and defrosted\n1 egg , beaten\nHeat the oil in a large, non-stick frying pan. Season the chicken to taste and fry for 5-8 mins until golden brown, turning occasionally. You may need to do this in two batches, depending on the size of your pan. Lift the chicken onto a plate and tip the bacon into the pan. Fry for 5 mins until crisp. Add the onion, mushrooms and thyme, then fry on a high heat for another 3 mins until the onions start to colour.\nTip the flour into the pan and cook, stirring, for 1 min. With the pan off the heat, gradually stir or whisk in the stock, followed by the milk, then add the chicken back to the pan. Bring to the boil, then simmer for 30 mins. Spoon the filling into a large pie or baking dish (approx 20 x 30cm) with a lip and leave to cool.\nHeat oven to 220C/fan 200C/gas 7. On a floured surface, roll the pastry to the thickness of two £1 coins. Cut a long strip as wide as the rim of the pie dish and, using a little of the egg, fix to the edge of the pie dish. Brush with egg, then lift the rest of the pastry over the pie, using the rolling pin to help. Gently press the edges with your fingers and trim with a sharp knife. Brush lightly with egg to glaze, then bake for 30 mins or until the pastry is risen and dark golden brown.",
        "category": "Main",
        "cloudFilename": "Chicken+pie",
        "timestamp": "2013-03-20T20:54:57Z",
        "title": "Chicken pie"
    },
    {
        "body": "Először megpirítunk kevés olajon két nagyobb fej, apróra vágott vöröshagymát, aztán  erre rádobunk úgy 60 dkg darált marhahúst, beszórjuk feketeborssal. \nKözben összetörjük és felaprózzuk a fokhagymagerezdeket, és ezt is beledobjuk a fazékba. \nEzután jön a friss erőspaprika. \nHa a hús már jó, fűszerezzük római köménnyel, oregánóval, kevés cayenne borssal, kevésbé erős chiliporral, majd pár percet kis lángon főzzük. \nLassan beledobálhatjuk a hámozott paradicsomokat is, meg néhány babérlevelet, sózni is ekkor lehet, nem kell. \nKét deci marhahús alaplevet is hozzáöntünk. Ne tobbet mert sos.\n\nTeszünk még három deci sört is a fazékba, a maradékot pedig igyuk meg (érdemes persze ezen kívül még egy sört meginni főzés közben, akkor jobban fog menni a fűszerezés). \nAmíg a sört kortyolgatjuk, forraljuk fel a fazék tartalmát, hogy a felesleges nedvességtartalom elpárologjon. \nHa még mindig túl híg, tegyünk bele egy-két evőkanál kukoricadarát. \nA végén tegyünk bele három konzerv babot, egy kis konzerv kukoricát, és három, különböző színű kaliforniai paprikát (sárgát, zöldet és pirosat). \nMég közepes lángon 10-15 percig főzzük, és kész.",
