        "body": "Hozzávalók 6 adagra:\nA pörkölthöz:\n700 g sertés apróhús (oldalas, dagadó, csülök)\n6 evőkanál étolaj\n1.5 közepes fej vöröshagyma\n2 gerezd fokhagyma\n1 kg savanyú káposzta\n300 ml víz\n0.5 teáskanál őrölt köménymag\n2 teáskanál fűszerpaprika\nsó\nA habaráshoz:\n350 ml tejföl (20%-os)\n30 g búzaliszt (BL55)\n1. A sertéshúst kockákra vágjuk, megtisztítjuk az inaktól, hártyáktól.\n2. A hagymákat megtisztítjuk, és finomra aprítjuk, felhevített olajon üvegesre pirítjuk.\n3. Rátesszük a kockára vágott húst, ízlés szerint sózzuk borsozzuk, hozzáadjuk a pirospaprikát, és párolás közben hozzáadjuk a fokhagymát, valamint az őrölt köményt is.\n4. A pörköltet felöntjük vízzel, és fedő alatt félpuhára pároljuk - kb 40 perc.\n5. A savanyú káposztát jó alaposan átmossuk, a pörkölthöz adjuk.\n6. Lefedve, lassan készre pároljuk. Mikor a hús készre párolódott, utánízesítjük sóval.\n7. Elkészítjük a habarást. A habaráshoz a tejfölt, a lisztet habverővel simára keverjük, a pörkölt levéből egy merőkanállal hozzákeverünk a tejfölhöz - hőkiegyenlítük. Ha ez kész, folyamatos keverés mellett, vékony sugárban adjuk a habarást a húshoz. Ezután folyamatos kevergetés mellett, még egyszer felfőzzük az ételt.\n8. Tálaláskor ügyeljünk, hogy a káposzta és a hús arányosan legyen elosztva, majd tejföllel és fűszerpaprikával díszítve tálaljuk.",
