        "body": "Hozzávalók 4 adaghoz:\n900 g mirelit spenót (2 zacskó)\n1 db zsemle\n200 ml habtejszín\n380 ml víz\n8 evőkanál étolaj\n2 púpozott teáskanál búzaliszt (BL55)\n4 gerezd fokhagyma\nízlés szerint só, bors\n1. A mirelit parajt kiolvasztjuk, lecsöpögtetjük.\n2. A zsemlét felkockázzuk, beáztatjuk a tejszín és a víz keverékébe.\n3. Olajat hevítünk, gyorsan hozzákeverjük a lisztet, hogy teljesen sima, csomómentes legyen.\n4. Belenyomjuk a fokhagymát, majd beletesszük a felengedett, lecsöpögtetett spenótot. Alaposan összekeverjük!\n5. Hozzáadjuk a beáztatott zsemlét, ezzel is alaposan elkeverjük.\n6. Ízlésünknek megfelelően sózzuk, borsozzuk, majd alaposan összeturmixoljuk - Robochef ™.\n7. Tükörtojással tálaljuk. Jó étvágyat!!",
