        "body": "Először megmossuk a rizst, sőt alaposan meg is kell dörzsölni a markunkban. Akkor jó, ha az öblítővíz már nem fehér. Leöntjük a vizet és kimérjük a főzővíz mennyiségét. Ez alapvetően 1.2-szer a száraz rizs térfogata, de kevés rizshez, nagyobb lábosban kicsit több kell. Egy vastag aljú lábosban (nem kell áztatni előtte) előbb közepes lángon addig főzzük, amíg a víz lecsökken a rizs szintjéig. Ekkor nagyon kis lángra állítjuk és még 10-15 percig főzzük letakarva egy jól záró fedővel, amit nem emelgetünk. Kikapcsolva lángot még 10 percig állni hagyjuk.\nHa kész a rizs, még melegen hozzákeverünk ecetet és cukrot. Ha a száraz rizs 3 dl volt, akkor 3 ek 10 százalékos ecet és 1 ek cukor kell. Jól elkeverjük és kihűtjük de nem tesszük hűtőbe.",
