    {
        "body": "50dkg liszt\n25 dkg margarin\n1 kk só !!!!\n1 db tojás\n5 dkg élesztőt 1 dl tejben megfuttatni+1 kanál cukor(én ezt porélesztővel helyettesítettem+langyos tej+cukor)\nÖsszegyúrni, 20 perc pihi.\nKinyújtani vékonyra és 15 dkg reszelt sajtot rászórni.\nFeltekerni mint a kakaós csigát.Kb 2 cm-esre lenyomkodni és szaggatni.\nTeteje tojássárgájával van megkenve.",
        "category": "Pastry",
        "cloudFilename": "Cheese+scones",
        "timestamp": "2012-04-07T11:37:32Z",
        "title": "Cheese scones"
    },
    {
        "body": "hozzávalók 8 főre\n    500 g finomliszt\n    250 g reszelt sajt (trappista)\n    250 g vaj v. margarin\n    250 g főtt áttört burgonya vagy túró\n    50 g élesztő\n    1 kis doboz tejföl\n    1 tojássárgája\n    1 tk méz vagy cukor\n    2 tk himalája só\ntetejére:\n    arany lenmag\nelkészítés\n    A tejfölt felmelegítjük kézmelegnél kissé melegebbre, a mézet belecsurgatjuk, az élesztőt belemorzsoljuk. Ezután meleg helyen hagyjuk, hogy felfusson.\n    A lisztet elmorzsoljuk a vajjal, majd hozzáadjuk a sót, az áttört főtt burgonyát, végül belekeverjük a reszelt sajtot is.\n    A tojássárgája és a tejfölben felfutott élesztő hozzáadásával laza tésztát gyúrunk, amit lisztezett deszkán vastagra nyújtunk. (Kb. 2 cm vastagságúra.)\n    A tésztát egy éles késsel berácsozzuk, megkenjük a fevert tojásfehérjével, és megszórjuk a magokkal.\n    Lisztbe mártott pogácsa szaggatóval kiszaggatjuk, és sütőpaírral bélelt tepsiben előmelegített sütőben készre sütjük! (gázsütő 6-os fokozat)\nsütési hőfok: 200°C",
        "category": "Pastry",
        "cloudFilename": "Cheese-potato+scones",
        "timestamp": "2013-10-05T05:06:16Z",
        "title": "Cheese-potato scones"
    },
    {
        "body": "* 500 g flour\n* 30 g fresh yeast\n* 250 g butter/margarine\n* 250 g feta cheese\n* 200 ml sour crème\n* 2 tsp salt\n* 50 ml milk\n* 1 tsp sugar\n* 1 scrambled whole egg or egg yolk\nDissolve the yeast in warm milk, add sugar. Use glass and wood not iron. Mix the flour with the butter and grated feta cheese well. Add sour crème and the salt, mix well then add the warm yeast. Leave it to rest for an hour.\nOn a work surface dusted with flour, roll out dough to 2 cm thick. Cut out rounds with a pastry cutter and place on an oven tray. Lightly brush tops of scones with the scrambled egg.\nCover the tray and place it while you heat the oven to 200 ºC. Bake them until the top is golden brown.\n---------\n* 50 dkg liszt\n* 3 dkg élesztő\n* 25 dkg vaj\n* 25 dkg feta sajt\n* 2 dl tejföl\n*  2 tk só\n* 0,5 dl tej\n* csipet cukor\nTetejére:\n* 1 jól felvert tojás\n    A langyos cukros tejben felfuttatjuk az élesztőt. A lisztet elmorzsoljuk a vajjal, majd a lereszelt fetasajttal, jól összedolgozzuk.\n    Hozzáadjuk a tejfölt, sót meg az élesztőt, és jól összegyúrjuk. Letakarva langyos helyen pihentetjük egy órát.\n    Enyhén lisztezett deszkára borítjuk, kicsit átgyúrjuk, majd 2 cm vastagra nyújtjuk.\n    Kiszaggatjuk, sütőpapírral bélelt tepsibe helyezzük, megkenjük a tojással.\n    Begyújtjuk a sütőt, amíg bemelegszik, addig a pogácsa letakarva pihen. Mielőtt berakjuk a sütőbe, még egyszer átkenjük, megszórhatjuk reszelt sajttal, de ez el is hagyható.\n    Berakjuk a forró sütőbe, és pirosra sütjük.\nsütési hőfok:\n200°C",
