    {
        "body": "Chicken and Chinese Leaf Salad\nFor a warm summer afternoon. Serves 4.\n1 chinese leaf cabbage, shredded\n1/2 onion, any colour, sliced very thinly\noptional additions: sliced cucumber, broccoli, carrot, bell pepper, avocado, tomato or bean sprouts\n4 handfuls of cooked chicken chunks\nDressing (mix together, and please adjust quantities to taste):\n1/2 clove crushed garlic\n2 teaspoons sesame oil\n1 tablespoon soy sauce\njuice of 2 limes or lemons\n1 teaspoon grated ginger\n1 tablespoon honey\n1 small minced green chilli\n2 tablespoons olive or nut oil\nCombine salad ingredients and toss with dressing. Done!",
        "category": "Sides",
        "cloudFilename": "Chicken+and+Chinese+Leaf+Salad",
        "timestamp": "2013-02-06T20:12:09Z",
        "title": "Chicken and Chinese Leaf Salad"
    },
    {
        "body": "- almapaprika\n- savanyú káposzta\n- gyorsíthatjuk almapaprikába töltött káposztával\n- virsli (ínyenceknek sütve)\n- főtt tojás fehérje\n- tejföl\n- majonéz\n- só\n- bors\n- foghagyma\nCsípősségét az almapaprika adja, de egy kis cseresznyepaprikával lehet erősíteni, ha túl csípős a tejföllel lehet lágyítani.",
        "category": "Sides",
        "cloudFilename": "Drunkard+salad",
        "timestamp": "2013-10-05T05:07:52Z",
        "title": "Drunkard salad"
    },
    {
        "body": "1 1/4 cups dried borlotti beans (about 1/2 pound)\n    1/2 cup coarsely chopped bacon (about 6 slices) \n    1 cup chopped onion (about 1 small) \n    1 cup chopped fennel bulb\n    3/4 cup chopped carrot (about 2 carrots) \n    3/4 cup chopped celery (about 2 stalks) \n    1 teaspoon dried basil\n    3 garlic cloves, minced\n    2 cups chopped zucchini (about 2 medium)\n    6 cups chopped kale\n    3 cups water\n    2 tablespoons tomato paste\n    2 (14-ounce) cans fat-free, less-sodium chicken broth \n    1 (14.5-ounce) can petite-diced tomatoes, undrained \n    1/4 teaspoon salt\n    1/4 teaspoon crushed red pepper\n    1/2 cup (2 ounces) grated fresh Parmigiano-Reggiano cheese\nPreparation\n    1. Sort and wash beans; place in a large bowl. Cover with water to 2 inches above beans; cover and let stand 8 hours or overnight. Drain beans.\n    2. Place beans in a large saucepan, and cover with water to 3 inches above beans; bring to a boil. Reduce heat; simmer 1 1/2 hours or until tender. Drain.\n    3. Heat a large Dutch oven over medium-high heat. Add bacon to pan; cook 6 minutes or until crisp, stirring frequently. Add onion and next 5 ingredients (through garlic); sauté 4 minutes. Add zucchini; sauté 3 minutes. Add kale; sauté 2 minutes or until kale wilts. Add 3 cups water, tomato paste, broth, and tomatoes; bring to a boil. Cover, reduce heat, and simmer 30 minutes. Add beans; simmer, uncovered, 30 minutes. Stir in salt and pepper; sprinkle with cheese.",
