        "body": "Az alaphoz:\n250 g lencse (száraz)\nszalonnabőr (füstölt)\n1000 ml alaplé - csont\n1 közepes fej hagyma - vörös\n2 db babérlevél\necet (10%-os)\n4 reszelésnyi citrom reszelt héja\n1 teáskanál mustár\nkristálycukor\nsó\nA rántáshoz:\n1 gerezd fokhagyma\n0.5 közepes fej hagyma - vörös\n4 evőkanál étolaj\n1 púpozott teáskanál búzaliszt (BL55)\n2 csipet pirospaprika\n1. A lencsét előző este alaposan átmossuk, beáztatjuk. Bő vízben hagyjuk állni egész nap. Sok vizet öntsünk rá, mert könnyen felszívja reggelre mindet!\n2. Az 1 l alaplevet feltesszük főni a kiázott, átmosott lencsével. Beleteszünk pár darab füstölt szalonnabőrt, az íze kedvéért, majd babérlevelet, az egész vöröshagymát, és sót adunk hozzá.\n3. Lefedjük, és addig főzzük, míg puhára nem fő a lencsénk. Ha szükséges, közben adjunk hozzá még csontlevet. Mikor kész, kivesszük belőle az egész vöröshagymát.\n4. Mikor kész a lencsénk, még a tűzön hagyjuk, és vele párhuzamosan gyorsan elkészítjük a rántást. Az olajat egy serpenyőbe tesszük, hozzáadjuk a finomra vágott vöröshagymát, megpirítjuk, hozzáadjuk a lisztet. Gyors kavargatással simára keverjük.\n5. Levesszük a tűzről, hozzáadjuk az összezúzott fokhagymát, és a pirospaprikát. Ehhez azért vesszük le a tűzről a rántást, hogy a paprika és a fokhagyma ne égjen meg, ha megég, keserű lesz.\n6. Kb 0.5 dl hideg vizet adunk hozzá, és simára keverjük.\nFONTOS! Csak hideg vizet adjunk a rántáshoz, hogy ne legyen csomós!\n7. A rántást hozzákeverjük a forrásban lévő lencséhez. Egyet forralunk rajta, majd ízlés szerint adunk hozzá sót, cukrot, mustárt, frissen reszelt citromhéjat, és kevés ecetet.\n8. A kész lencsefőzeléket füstölt csülökkel tálaljuk, ízlés szerint, tálalás előtt tejfölt keverhetünk hozzá ízlés szerint.",
