        "body": "1 közepes cukkini, lereszelve\negy nagy marék rukkola (egy 5dkg-os zacskó fele)\n1 kicsi hagyma, apróra vágva\n2 nagy gerezd fokhagyma, vékonyra szeletelve\n5 dl húsleves\n1/2 dl tejszín\n3-4 dkg pecorino, vagy parmezán, reszelve\nsó, bors, szerecsendió\n2 evőkanál vaj, vagy olívaolaj\nElkészítés módja:\nA vajon, vagy olívaolajon üvegesre pirítjuk a hagymát és fokhagymát. Rádobjuk a lereszelt (héjával együtt) cukkinit. Átforgatjuk, pár percig pároljuk. Felöntjük a húslevessel, pár percig főzzük, hogy a cukkini puha legyen. Beleszórjuk a reszelt sajtot és beledobjuk a rukkolát. A rukkola azonnal összeesik. Botmixerrel, vagy robotgépben pürésítsük a levest. Beleöntjük a tejszínt (elhagyható, önmagával sürítve is jó lesz, de azért jobb a tejszínnel), összerottyantjuk. Végül ízlés szerint sóval, borssal és szerecsendióval ízesítjük.",
