        "body": "Hozzávalók:\n1 közepes fej hagyma\n20 dkg. füstölt kolbász, \n1 kiskanál sertészsír, \n50 dkg. savanyú káposzta, \nsó, \nőrölt fekete bors, \n1 dl. száraz fehér bor, \n1 kiskanál köménymag, \n1-2 db. kisebb babérlevél, \n1,5 liter alaplé \n1 dl. tejföl\nElkészítés:\nA kevés zsíron a felszeletelt kolbászt átforrósítjuk, belepergetjük a köménymagot, hogy kissé kipattogjon.Aláöntjük a bort és hozzáadjuk a szálas savanyú káposztát. Pár percig - kb.5-8 percig - együtt pároljuk majd felöntjük az alaplével. Ízesítjük ízlés szerint, főzzük tovább 15-20 percig.Készre főzés előtt tejfölt keverünk hozzá.",
