    {
        "body": "* 1 tbsp vegetable oil\n* 1/2 tbsp grated fresh ginger\n* 1/8 stalk lemon grass, minced\n* 1/2 tsp red curry paste\n* 1 cup chicken broth\n* 1 tbsp fish sauce\n* 2 tsp light brown sugar\n* 1/2 can coconut milk\n* 30g fresh shiitake mushrooms, sliced\n* 2 tablespoons fresh lime juice\n* salt to taste\n* 1 tsp chopped coriander\n    Heat the oil in a large pot over medium heat. Cook and stir the ginger, lemon grass, and curry paste in the heated oil for 1 minute. Slowly pour the chicken broth over the mixture, stirring continually. Stir in the fish sauce and brown sugar; simmer for 15 minutes. Stir in the coconut milk and mushrooms; cook and stir until the mushrooms are soft, about 5 minutes. Cook until no longer translucent about 5 minutes. Stir in the lime juice; season with salt; garnish with fresh coriander.",
